import style from './Skills.module.scss'
import {MSkill, Skill} from "./Skill";
import react_logo from '../../Images/icons/Skills_logo/react.svg'
import html_logo from '../../Images/icons/Skills_logo/html5.svg'
import css_logo from '../../Images/icons/Skills_logo/css.svg'
import js_logo from '../../Images/icons/Skills_logo/js.svg'
import redux_logo from '../../Images/icons/Skills_logo/redux.svg'
import typescript_logo from '../../Images/icons/Skills_logo/typescript.png'

import github_logo from '../../Images/icons/Skills_logo/github.gif'
import sass_logo from '../../Images/icons/Skills_logo/sass.svg'



const rightAnimation = {
    hidden: {
        y: 200,
        opacity: 0,
    },
    visible: custom => ({
        y: 0,
        opacity: 1,
        transition: {delay: custom * 0.3}
    })
}
const leftAnimation = {
    hidden: {
        y: -200,
        opacity: 0,
    },
    visible: custom => ({
        y: 0,
        opacity: 1,
        transition: {delay: custom * 0.3}
    })
}
export const Skills =()=> {


    return (
        <div className={style.skills_container}
             initial='hidden'
             whileInView='visible'
             viewport={{once: true}}
            >

            <MSkill title={'HTML'} logo={html_logo} info={'HTML        -  .'}/>
            <MSkill title={'CSS'} logo={css_logo} info={'CSS       ,     .'}/>
            <MSkill title={'JS'} logo={js_logo} info={'JavaScript    .'}/>
            <MSkill title={'React'} logo={react_logo} info={'React  JavaScript-    .'}/>
            <MSkill title={'Redux'} logo={redux_logo} info={'Redux    JavaScript     .'}/>
            <MSkill title={'TypeScript'} logo={typescript_logo} info={'TypeScript     -,   JavaScript.'}/>
            <MSkill title={'GitHub'} logo={github_logo} info={'GitHub   -   IT-    .'}/>
            <MSkill title={'Sass'} logo={sass_logo} info={'Sass   ,      CSS-      .'}/>

        </div>
    )
}